import axios from 'axios';
import { request } from 'http';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

interface MyCustomRequestOptions extends RequestInit {
  gameType: string;
  gameName: string;
  boardName: string;
}

function CreatePost() {
  const navigate = useNavigate();
  const [gameType, setGameType] = useState('');
  const [gameName, setGameName] = useState('');
  const [boardName, setBoardName] = useState('');
  const [postTitle, setPostTitle] = useState('');
  const [postContent, setPostContent] = useState('');
  const [selectedImage, setSelectedImage] = useState<File | null>(null);

  const handleGameTypeChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setGameType(e.target.value);
  };

  const handleGameNameChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setGameName(e.target.value);
  };

  const handleBoardNameChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setBoardName(e.target.value);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    switch (name) {
      case 'postTitle':
        setPostTitle(value);
        break;
      case 'postContent':
        setPostContent(value);
        break;
      default:
        break;
    }
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    setSelectedImage(file || null);
  };

  const handleSubmit = async (event: React.FormEvent<HTMLButtonElement>) => {
    event.preventDefault();

    const requestData = {
      postTitle: postTitle,
      postContent: postContent,
    };

    try {
      const accessToken = localStorage.getItem('accessToken');
      const refreshToken = localStorage.getItem('refreshToken');

      if (!accessToken) {
        console.error('  .');
        alert(' ');
        navigate("/");
        return;
      }

      const formData = new FormData();
      formData.append('requestDto', new Blob([JSON.stringify(requestData)], { type: "application/json" }));
      if (selectedImage) {
        formData.append('file', selectedImage);
      }

      const response = fetch(`http://51.21.48.160:8080/api/posts?gameType=${gameType}&gameName=${gameName}&boardName=${boardName}`, {
        method: 'POST',
        body: formData,
        headers: {
          //'Content-Type': 'multipart/form-data', <<  !!!
          'Access': `${accessToken}`,
          'Refresh': `${refreshToken}`,
        },
      }).then(async response => {
        if (response.ok) {
          const responseData = await response.json();
          console.log(responseData);
          const postId = responseData.data.postId;
          handleSuccess(postId);
        } else {
          handleFailure();
        }
      })

    } catch (error) {
      console.error(" " + error);
    }
  };

  const handleSuccess = (postId: number) => {
    console.log('  ');
    alert('  ');
    navigate(`/api/posts/${postId}`);
  };

  const handleFailure = () => {
    console.error('  ');
    alert('  ');
    window.location.reload()
  };

  const handleCancel = () => {
    navigate(-1);
  };

  return (
    <div className="container mt-5">
      {/*    */}
      <div className="mb-3 d-flex">
        <div className="me-3">
          <label htmlFor="gameType" className="form-label">
              
          </label>
          <select
            className="form-select"
            id="gameType"
            value={gameType}
            onChange={handleGameTypeChange}
          >
            <option value="">
                
            </option>
            <option value="PC_GAME">PC</option>
            <option value="CONSOLE_GAME"></option>
            <option value="MOBILE_GAME"></option>
            <option value="EMPTY_TYPE"></option>
          </select>
        </div>
        <div className="me-3">
          <label htmlFor="gameName" className="form-label">
             
          </label>
          <select
            className="form-select"
            id="gameName"
            value={gameName}
            onChange={handleGameNameChange}
          >
            <option value="">
               
            </option>
            <option value="LEAGUE_OF_LEGEND"></option>
            <option value="VALORANT"></option>
            <option value="THE_LEGEND_OF_ZELDA_TEARS_OF_THE_KINGDOM">     </option>
            <option value="BRAWL_STARS"></option>
            <option value="EMPTY_NAME"></option>
          </select>
        </div>
        <div>
          <label htmlFor="boardName" className="form-label">
             
          </label>
          <select
            className="form-select"
            id="boardName"
            value={boardName}
            onChange={handleBoardNameChange}
          >
            <option value="">
               
            </option>
            <option value="FREE_BOARD"> </option>
            <option value="FIND_USER_BOARD">  </option>
            <option value="GROUP_PROMOTION_BOARD">  </option>
            <option value="REQUIRED_BOARD"> </option>
          </select>
        </div>
      </div>

      <div className="container mt-5">
        {/*    */}
        <div className="mb-3 d-flex flex-column align-items-start">
          <label htmlFor="postTitle" className="form-label mb-2">
            
          </label>
          <input
            type="text"
            className="form-control mb-3"
            id="postTitle"
            onChange={(e) => setPostTitle(e.target.value)}
            placeholder=" "
          />
           
          {/*   */}
          <label htmlFor="image" className="form-label me-2 mb-2">
             
          </label>
          <input
            type="file"
            className="form-control mb-3"
            id="image"
            accept="image/*"
            onChange={handleImageChange}
          />

          <label htmlFor="postContent" className="form-label mb-2">
            
          </label>
          <textarea
            className="form-control mb-3"
            id="postContent"
            onChange={(e) => setPostContent(e.target.value)}
            rows={4}
            placeholder=" "
          ></textarea>
        </div>
        <div className="mb-3 d-flex justify-content-center">
          <button type="button" className="btn btn-primary me-2" onClick={handleSubmit}>
            
          </button>
          <button type="button" className="btn btn-secondary" onClick={handleCancel}>
            
          </button>
        </div>
      </div>
    </div>
  );
}

export default CreatePost;
