import React, { useEffect, useState } from 'react';
import { Nav, Container, Form, Navbar, NavDropdown } from 'react-bootstrap';
import { NavLink, useNavigate } from 'react-router-dom';
import './TopNav.css'; // Import the CSS file

import { jwtDecode } from "jwt-decode";

interface JwtPayload {
	//       .
	auth?: string; // auth  optional    
	exp?: number;
  }

function TopNav() {
	const [isLoggedIn, setIsLoggedIn] = useState(false);
	const [isAdmin, setIsAdmin] = useState(false);
	const navigate = useNavigate();

	
	useEffect(() => {
		const accessToken = localStorage.getItem('accessToken');
	
		const handleLogout = () => {
		  //     (:  ,  API  )
		  localStorage.removeItem('accessToken');
		  setIsLoggedIn(false);
		};
	
		if (accessToken) {
		  const token = accessToken.substring(7); // "Bearer " 
		  const decoded:JwtPayload = jwtDecode(token);
		  const currentTimestamp = Math.floor(Date.now() / 1000);
	
		  if (decoded.exp && decoded.exp < currentTimestamp) {
			//     
			handleLogout();
		  } else {
			//      
			setIsLoggedIn(true);
			if (decoded.auth && decoded.auth === 'ADMIN') {
				//  'ADMIN'   , isAdmin  true 
				setIsAdmin(true);
			  }
		  }
		} else {
		  //     
		  handleLogout();
		}
	  }, []);

	return (
		<Navbar expand="lg" className="top-nav">
			<Container fluid>
				<Navbar.Toggle aria-controls="navbarScroll" />
				<Navbar.Collapse id="navbarScroll">
					<Nav
						className="me-auto my-2 my-lg-0"
						style={{ maxHeight: '400px', margin: '10 auto 10 auto' }}
						navbarScroll
					>
						<Nav.Link><NavLink to='/' style={{ textDecoration: 'none', color: 'inherit' }}></NavLink></Nav.Link>
						<NavDropdown title="PC" id="navbarScrollingDropdown">
							<NavDropdown.Item><NavLink to="/pc/lol">LOL</NavLink></NavDropdown.Item>
							<NavDropdown.Item><NavLink to="/pc/val"></NavLink></NavDropdown.Item>
						</NavDropdown>
						<NavDropdown title="" id="navbarScrollingDropdown">
							<NavDropdown.Item><NavLink to="/console/zelda"></NavLink></NavDropdown.Item>
						</NavDropdown>
						<NavDropdown title="" id="navbarScrollingDropdown">
							<NavDropdown.Item><NavLink to="/mobile/brawl_stars"></NavLink></NavDropdown.Item>
						</NavDropdown>
						<Nav.Link><NavLink to='/team_promotion' style={{ textDecoration: 'none', color: 'inherit' }}>  </NavLink></Nav.Link>
						<Nav.Link><NavLink to='/request' style={{ textDecoration: 'none', color: 'inherit' }}> </NavLink></Nav.Link>
						{ isAdmin && <Nav.Link><NavLink to='/admin/user_list' style={{ textDecoration: 'none', color: 'inherit' }}></NavLink></Nav.Link> }
					</Nav>
					<Form className="d-flex">
						{/*      . */}
						{!isLoggedIn ? (
							<>
								<Nav.Link><NavLink to='/signup' style={{ textDecoration: 'none', color: 'inherit' }}></NavLink></Nav.Link>
								<span className="mx-2"></span>
								<Nav.Link><NavLink to='/login' style={{ textDecoration: 'none', color: 'inherit' }}></NavLink></Nav.Link>
							</>
						) : (
							<>
								<Nav.Link><NavLink to='/my_page' style={{ textDecoration: 'none', color: 'inherit' }}> </NavLink></Nav.Link>
								<span className="mx-2"></span>
								<Nav.Link><NavLink to='/logout' style={{ textDecoration: 'none', color: 'inherit' }}></NavLink></Nav.Link>
							</>
						)}
					</Form>
				</Navbar.Collapse>
			</Container>
			</Navbar>
	);
}

export default TopNav;